body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.notification {
  margin-left: 20px;
  font-size: 16px;
  color: rgb(189, 0, 0);
}

.openModal {
  width: 35px;
  height: 35px;
  position: absolute;
  top: 17px;
  right: 5px;
}
.closeModal {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 5px;
  right: 4px;
}
